@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&family=Roboto:ital,wght@1,700&display=swap');
@import url("https://fonts.googleapis.com/css2?family=M+PLUS+1+Code&display=swap");



*{
    font-family:'Roboto' ,'sans-serif';
    font-size:15px;
    
}

body{
    display:flex;
    justify-content: center;
    align-items: center;
    background-color: #457b9d;;
}

h1{
    color: #e63946;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
}

.container{
     top:50%;
     left:50;   
     position: absolute;
     transform: translate(-50%,-50%);
     margin:0 auto;
     width:256px;
     text-align:center;
     border:10px solid #101110;
     background-color: #101116;
}

.keypad{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px,auto);
}

input[type = "text"]{
    height: 75px;
    width:245px;
    background-color: #2b2a2c;
    border: 2px solid rgb(33, 127, 250);
    color: #96f817;
    text-align: right;
    font-family: "M PLUS 1 Code", sans-serif;
    font-weight: 500;
    font-size: 25px;
    letter-spacing: 1px;
}

#clear{
    grid-column: 1/3;
    grid-row: 1;
    color: #262834;
}

#result {
    grid-column: 3/5;
    grid-row: 5;
    color: #262834;
}
#backspace{
    color: #262834;
}

button{
    margin: 5px;
    border: none;
    border-radius:5px;
    cursor: pointer;
    background:#262834;
    color:#ffffff;
    font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active{
     
    outline: none;
}

.highlight{
    background: palevioletred;
}